.container-app{
    background-color: #3e1229;
    
    font-family: fantasy;
}


.title{
    font-size: 40px;
    text-align: center;
    color: #c7a290;
    padding: 20px;
    font-family:fantasy;
    font-weight: 500;
}
.owen{
    width: 280px;
    display: block;
    margin: auto;
    border-radius: 50%;
    display: block;
    border:2px solid rgb(199, 162, 144) 
    
}

.container-item{
   
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #b3b2d2;
    margin: 50px;
    border-radius: 30%;
    padding: 20px 0;
    color: #3e1229;
    border: 5px solid white;
    
    
    
    
}
.list{
    width: 360px;
    display: flex;
    flex-direction: column;
    margin: auto;
    list-style: none;
    

}

.text{
    font-weight: 200;
}
.link-info{
    text-decoration: none;
    color: #3e1229;
}
.link-info:hover{
    color: #ea7465;
}
.poster{
    width: 150px;
    border-radius: 5px;
    border: 2px white solid;
}
@media all and (min-width: 700px) {
    .title{
        font-size: 70px;
    }
    .owen {
        width: 350px;
    }
    .list{
       
       display: grid;
       grid-template-columns: auto auto;
       justify-content: flex-start;
       width: 600px;
        
    }
    .container-item{
        margin: 20px;
        width: 250px;
        height: 400px;
    }
  }